@import '_common.scss';

//keyframe
//페이지 로드시 들어가는 에니메이션
@keyframes show1 {
    from { transform: translateX(80%);}
    100%, to { transform: translateX(0%);}
}
@keyframes show2 {
    from { transform: translateY(16%); }
    100%, to { transform: translateY(0%); }
}
@keyframes show3 {
    from { transform: translateX(-20%); }
}
@keyframes show4 {
    from { transform: translateY(-20%); }
    100%, to { transform: translateY(0%); }
}
@keyframes show5 {
    from { transform: translateY(-80%); }
    100%, to { transform: translateY(0%); }
}

@keyframes show6 {
    from { transform: translateX(20%); }
}

// 메인텍스트 돌아가는 애니메이션
@keyframes text{
    0%, 16% { transform: translateY(0%); }
    25%, 41% { transform: translateY(100%); }
    50%, 66% { transform: translateY(200%); }
    75%, 91% { transform: translateY(300%); }
    100% { transform: translateY(400%); }
}
// 스크롤 아이콘 움직이는 애니메이션
@keyframes scroll{
    0% {transform: translateY(0px);}
    50% {transform: translateY(-40px);}
    100% {transform: translateY(0px);}
}
// 슬라이드 애니메이션
@keyframes slide1 {
    from   { transform: translate(0%);}
    99%,to { transform: translate(-100%);}
}
@keyframes slide2 {
    from   { transform: translate(-100%);}
    99%,to { transform: translate(0%);}
}

main{
    position: relative;
}
.home-content1{
    height: calc(100vh - 160px);
    position: relative;
    a span{
        color: $sc;
        font-size: 24px;
        position: absolute;
        right: 20px; top: 20vh;
        animation: show1 1s ease;
    }
    a:hover span{
        text-decoration: underline;
    }
    .main-title{
        padding-top: 24vh;
        animation: show2 1s ease;
        span, p span{
            color: $st;
            font-size: 80px;
            font-weight: 700;
            letter-spacing: 0.04em;
        }
        h3 {
            height: 140px;
            display: inline-block;
            transform: translateY(16px);
            overflow: hidden;
            div{
                width: 600px;
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: left;
                transform: translateY(-80%);
            }
            span{
                display: inline-block;
                color: $mc;
                font-size: 116px;
                font-weight: 700;
                letter-spacing: 0.04em;
                animation: text 10s infinite;
            }
        }
        p, b{
            font-size: 116px;
            font-weight: 700;
            letter-spacing: 0.04em;
        }
        sup{
            color: $sc;
            font-size: 60px;
            font-weight: 400;
        }
    }
}

// ================================================================================
// ============================= about ============================================
// ================================================================================

.cv{
    position: relative;
    .scroll{
        position: absolute;
        left: 200px; bottom: -160px;
        img{
            animation: scroll 2s linear infinite;
        }
    }
    .cv-name{
        position: absolute;
        left: 20px; top: -80px;
        animation: show3 1.2s ease;
        p{
            font-size: 80px;
            font-weight: 700;
            letter-spacing: 0.08em;
        }
        sup{
            color: $sc;
            font-size: 40px;
            font-weight: 700;
            letter-spacing: 0;
            line-height: 100px;
        }
        p:nth-of-type(2){
            margin-left: 60px;
            color: $mc;
            font-family: Arial, Helvetica, sans-serif;
            font-size: 60px;
            text-transform: capitalize;
            letter-spacing: 0.04em;
        }
    }
    .cv-profile{
        width: 848px; height: 478px;
        margin: 180px 0 292px 432px;
        img{
            animation: show4 1.2s ease;
        }
    }
    .cv-email{
        position: absolute;
        right: 38px; bottom: -104px;
        animation: show2 2s ease;
        p{
            font-family: Arial Black,Arial Bold,Gadget,sans-serif;
            font-size: 24px;
            font-weight: 700;
            letter-spacing: 0.02em;
            line-height: 1.4;
        }
        p:nth-of-type(1){
            color: $st;
        }
        p:nth-of-type(2){
            text-transform: lowercase;
        }
        &::before{
            content: '';
            height: 54px;
            border-right: 4px solid $sc;
            position: absolute;
            right: 288px; bottom: 6px;
        }
    }
}
.slide-ani{
    div{
        height: 80px;
        margin-bottom: 20px;
        border-top: 1px solid $st;
        border-bottom: 1px solid $st;
        display: flex;
        overflow: hidden;
    }
    span{
        margin: 8px 0 0 8px;
        display: inline-block;
        white-space: nowrap;
        font-size: 48px;
        font-weight: 700;
        color: $st;
    }
    i{
        font-size: 48px;
        font-weight: 700;
        color: $mc;
    }
    b{
        font-size: 48px;
        font-weight: 700;
        color: $sc;
        font-style: italic;
    }
    .slide1{
        animation: slide1 40s linear infinite;
    }
    .slide2{
        animation: slide2 40s linear infinite;
    }
}
.cv-text{
    margin: 240px 0 120px;
    p{
        text-align: center;
    }
    p img{
        width: 92px;
    }
    .cv-title{
        margin: 80px 0;
        font-size: 24px;
        font-weight: 100;
        letter-spacing: 0.02em;
        b{
            font-size: 24px;
            font-weight: 400;
            letter-spacing: 0.02em;
        }
    }
    .cv-letter{
        line-height: 1.8;
        letter-spacing: 0.02em;
        position: relative;
        &::after{
            content: '';
            width: 80px;
            border: 4px solid $sc;
            position: absolute;
            left: calc(50% - 40px); bottom: -240px;
        }
    }
}
.education{
    margin: 480px 0 240px;
    text-align: center;
    h4{
        margin: 40px 0;
    }
    p{
        line-height: 1.8;
        letter-spacing: 0.02em;
    }
}
.skill{
    text-align: center;
    margin-bottom: 400px;
    h4{
        margin: 80px 0 40px;
    }
    h4:nth-of-type(2),
    h4:nth-of-type(3){
        text-transform: none;
    }
    div{
        margin: 80px 0 200px;
    }
    p{
        margin: 16px;
        // font-size: 16px;
        // font-weight: 100;
        letter-spacing: 0.02em;
        line-height: 1.6;
        text-transform: none;
    }
    p img{
        width: 92px;
        margin: 0 16px;
    }
}

// ================================================================================
// ============================= works ============================================
// ================================================================================

.works{
    height: calc(100vh - 160px);
    .work-title{
        padding: 80px 20px 160px;
        display: flex;
        align-items: flex-end;
        animation: show4 1s ease;
        h3{
            font-family: Arial Black,Arial Bold,Gadget,sans-serif;
            font-size: 60px;
            letter-spacing: 0.04em;
        }
        p{
            margin: 0 20px 4px;
            font-family: Arial Black,Arial Bold,Gadget,sans-serif;
            font-size: 40px;
            letter-spacing: 0.04em;
            color: $mc;
        }
    }
    .work-list{
        width: 100%;
        .work-padding{
            overflow: hidden;
        }
        .list-wrap{
            display: flex;
            align-items: flex-start;
            justify-content:flex-start;
            transition: .8s;
            animation: show6 1s ease;
        }
        .next{
            margin: 300px 32px 0 20px;
            img{
                width: 17px;
            }
        }
        .pre{
            margin: 300px 20px 0 32px;
            img{
                width: 17px;
            }
        }
        .work-item{
            width: 524px;
            margin: 0 16px;
            h4{
                width: 524px;
                color: $sc;
            }
            p{
                width: 100%;
                margin-bottom: 20px;
                font-size: 40px;
                font-weight: 700;
                letter-spacing: 0.04em;
                line-height: 1;
                color: $st;
            }
            .img-wrap{
                width: 100%; height: 295px;
                overflow: hidden;
                img{
                    transition: .5s;
                }
            }
            &:hover{
                p{
                    color: #000;
                }
                img{
                    transform: scale(1.1);
                }
            }
        }
    }
}

.work-detail{
    margin-bottom: 400px;
    text-align: center;
    position: relative;
    .navi{
        width: 100%;
        display: flex;
        justify-content: space-between;
        transform: translateY(140px);
        button{
            padding: 0;
            font-family: Arial Black,Arial Bold,Gadget,sans-serif;
            color: $st;
            &:hover{
                color: $sc;
            }
        }
    }
    h3{
        margin: 80px 0 8px;
        font-size: 40px;
        font-weight: 800;
        letter-spacing: 0.04em;
        display: inline-block;
        position: relative;
        animation: show5 1s ease;
    }
    .summary{
        width: 848px;
        margin: 0 auto;
        font-weight: 100;
        line-height: 1.6;
        animation: show4 1s ease;
    }
    .linkTo{
        display: inline-block;
        width: 180px; height: 40px;
        margin: 40px 0 120px;
        padding-top: 8px;
        background-color: $mc;
        color: #fff;
        font-weight: 700;
        animation: show4 1s ease;
        &:hover{
            color: #000;
            background-color: $sc;
        }
    }
    .video{
        margin-bottom: 160px;
        animation: show2 1s ease;
    }
    .detail-text{
        width: 740px;
        margin: 80px auto;
        display: flex;
        p{
            text-align: left;
        }
        .text-small{
            width: 416px;
        }
        b{
            display: block;
            margin-bottom: 8px;
            font-size: 20px;
            font-weight: 700;
        }
        span{
            font-size: 20px;
            font-weight: 100;
            letter-spacing: 0.02em;
            line-height: 1.4;
        }
    }
}

// ================================================================================
// ============================= contact ==========================================
// ================================================================================

.contact{
    width: 320px; height: calc(100vh - 160px);
    margin: 0 auto;
    padding-top: 100px;
    .inq{
        margin: 60px 0 40px;
        display: flex;
        justify-content: space-between;
        span:nth-last-of-type(1){
            // font-weight: 700;
        }
        span:nth-last-of-type(1){
            text-transform: none;
            line-height: .8;
            font-weight: 100;
        }
    }
    form{
        p{
            margin: 12px 0;
            display: flex;
            justify-content: space-between;
        }
        label{
            font-family: Arial, Helvetica, sans-serif;
            font-size: 20px;
        }
        p:nth-of-type(1) label,
        p:nth-of-type(2) label {
            &::after{
                content: '*';
                margin: 0 0 8px 4px;
                color: $mc;
                font-weight: 700;
            }
        }
        input{
            width: 240px;
            border-bottom: 2px solid $sc;
            text-transform: none;
        }
        p:nth-of-type(3){
            display: block;
            margin: 40px 0 20px;
            label{
                // &::after{
                //     content: '1000자';
                //     margin-left: 112px;
                //     font-family: 'Noto Sans KR', sans-serif;
                //     font-weight: 400 !important;
                //     color: $mc;
                //     font-size: 12px;
                //     font-weight: 700;
                // }
            }
        }
        textarea{
            width: 320px; height: 240px;
            margin-top: 8px;
            padding: 4px;
            border: 2px solid $sc;
            text-transform: none;
            resize: none;
            /* resize:none; 크기고정
                resize: horizontal; // 가로크기만 조절가능 
                resize: vertical;  세로크기만 조절가능  */
        }
        p:nth-of-type(4){
            justify-content: flex-end;
        }
        button{
            width: 160px; height: 32px;
            background-color: $mc;
            color: #fff;
            font-family: Arial, Helvetica, sans-serif;
            // font-family: Arial Black,Arial Bold,Gadget,sans-serif;
            font-weight: 700;
            line-height: 1;
        }
    }
}

.thanks{
    height: calc(100vh - 160px);
    padding: 360px 0;
    text-align: center;
    h4{
        
        font-size: 32px;
        &:hover{
            color: $mc;
        }
    }
    h4:hover+p{
        display: block;
        color: $sc;
    }
    p{
        color: $mc;
        font-family: Arial, Helvetica, sans-serif;
        text-transform: none;
    }
}